import React, { useEffect, useState } from "react";
import "./App.css";

function App() {
  let img ={
    path:'/img/background-1.gif',
    size:'cover'
  }
  const [background, setBackground] = useState(img);
  
  const isEmpty = (v) => {
    return Object.keys(v).length === 0;
  };
  const calculateTimeLeft = () => {
    const target = '2022-2-1';
    const difference = new Date(target) - +new Date();
    let timeLeft = {};
    
    if (difference > 0) {
      timeLeft = {
        ngày: Math.floor(difference / (1000 * 60 * 60 * 24)),
        giờ: Math.floor((difference / (1000 * 60 * 60)) % 24),
        phút: Math.floor((difference / 1000 / 60) % 60),
        giây: Math.floor((difference / 1000) % 60),
      };
    }
    
    return timeLeft;
  };
  
  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());

  useEffect(() => {
    setTimeout(() => {
      setTimeLeft(calculateTimeLeft());
    }, 1000);
    // if(isEmpty(calculateTimeLeft())){
    //   setBackground({
    //     path:'/img/background.gif',
    //     size:'cover'
    //   })
    // }
  });

  const timerComponents = [];

  Object.keys(timeLeft).forEach((interval) => {
    // if (!timeLeft[interval]) {
    //   return;
    // }

    timerComponents.push(
      <span>
        <span className="time">{timeLeft[interval]}</span> {interval}{" "}
      </span>
    );
  });
  return (
    <div className="background"
          style={{ 
            backgroundImage: `url(${process.env.PUBLIC_URL + background.path})`,
            backgroundSize: background.size
          }}
    >
      <div className="card time-box">
      {timerComponents.length ? timerComponents : <span className="hny-text">Chúc mừng năm mới</span>}
      </div>
    </div>
  );
}

export default App;
